import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { getStyles } from "./style";
import AttachFileIcon from '@material-ui/icons/AttachFile';
import IconButton from './IconButton';
import withUtils from './utils/withUtils';

const styles = theme => ({
  ...getStyles(theme),
  button: {
    margin: theme.spacing.unit
  }
});

class ImageAvatars extends React.Component {
  constructor(props) {
    super(props);

    this.inputRef = React.createRef();
  }

  onChange = async (event) => {
    event.preventDefault();

    let file = event.target.files[0];
    if(this.props.mimeType === 'image/jpeg') {
      var urlCreator = window.URL || window.webkitURL;
      var src = urlCreator.createObjectURL(file);
      file = await this.props.utils.resize(src, 640, file.name);
    }

    this.props.onChange(this.props.field, file)
  }

  onClick = () => {
    if(!this.props.disabled) {
      this.inputRef.current.click();
    }
  }

  render() {
    const { classes, value, disabled, mimeType } = this.props;

    return (
      <div>
        <IconButton className={classes.button} tooltip="Attach file" color={value ? 'primary' : 'default'} onClick={this.onClick} disabled={disabled}>
          <AttachFileIcon />
        </IconButton>
        <input
          ref={this.inputRef}
          style={{ display: "none" }}
          type="file"
          accept={mimeType}
          onChange={this.onChange}
        />
      </div>
    );
  }
}

ImageAvatars.propTypes = {
  classes: PropTypes.object.isRequired,
  field: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  value: PropTypes.string.isRequired,
  disabled: PropTypes.bool,
  mimeType: PropTypes.string
};

export default withUtils(withStyles(styles)(ImageAvatars));
