import React, { PureComponent } from "react";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip } from "recharts";
import compose from "../../compose";
import withStore from "../../consumers/withStore";
import { withTheme } from '@material-ui/core/styles';

const styles = theme => ({
  paper: {
    width: "100%",
    padding: theme.spacing.unit,
    overflowX: "auto",
    margin: "auto"
  }
});

class Example extends PureComponent {
  render() {
    const { dateStats } = this.props.store;
    const { theme } = this.props;

    return (
      <Paper className={this.props.classes.paper}>
        <BarChart width={800} height={300} data={dateStats.data}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis />
          <Tooltip />
          {/* <Legend /> */}
          <Bar dataKey="duration" fill={theme.palette.primary.main} />
        </BarChart>
      </Paper>
    );
  }
}

export default compose(withStore, withStyles(styles), withTheme())(Example);
