import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import TextInput from "../shared/TextInput";
import Form from "../shared/form/Form";
import Button from "@material-ui/core/Button";
import Dialog from "../shared/Dialog";
import AvatarPicker from "../shared/avatar/AvatarPicker";

import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import compose from "../../compose";
import Switch from "../shared/Switch";
import { getStyles } from "../shared/style";
import config from "../../config.json";
import { withLocalization } from "../shared/localization";
import _ from 'lodash';

const styles = theme => ({
  ...getStyles(theme),
  heading: {
    marginLeft: theme.spacing.unit
  },
  detailHeading: {
    marginLeft: theme.spacing.unit,
    marginTop: theme.spacing.unit * 3
  },
  actions: {
    float: "right",
    margin: theme.spacing.unit
  }
});

const INIT_STATE = {
  password: {
    value: "",
    errorText: ""
  },
  repeatedPassword: {
    value: "",
    errorText: ""
  },
  email: {
    value: "",
    isLoading: false,
    errorText: ""
  },
  name: "",
  username: {
    value: "",
    isLoading: false,
    errorText: ""
  },
  tel: "",
  address: "",
  city: "",
  postCode: "",
  country: "",
  otp: false,
  avatar: null
};

const buildState = (user) => {
  return {
    ...INIT_STATE,
    ...user,
    email: { ...INIT_STATE.email, value: user.email },
    username: { ...INIT_STATE.username, value: user.username },
    isOpenSecretDialog: false,
    avatarChanged: false
  };
}

class FormWrapper extends React.Component {
  state = buildState(this.props.user.user);

  handleChange = (name, value) => {
    this.setState((state) => _.set(state, name, value));
  };

  validPasswords = () => {
    const { password, repeatedPassword } = this.state;
    const { translate } = this.props;

    const regex = new RegExp(config.passwordPattern);
    const isValidPassword = !password.value || regex.test(password.value);
    const errorTextPassword = isValidPassword ? "" : translate('field.passwordWeak');

    const isValidRepeatedPassword = password.value === repeatedPassword.value;
    const errorTextRepeatedPassword = isValidRepeatedPassword ? "" : translate('field.passwordNoMatch');

    this.setState((state) => ({ 
      password: { ...state.password, errorText: errorTextPassword },
      repeatedPassword: { ...this.state.repeatedPassword, errorText: errorTextRepeatedPassword }
    }));

    return isValidPassword && isValidRepeatedPassword;
  }

  validEmail = async () => {
    const { translate} = this.props;
    const { email } = this.state;

    this.setState((state) => ({ email: {
      ...state.email,
      isLoading: true
    }}));

    const isValid = !email.value || await this.isValid(email.value);
    const errorText = isValid ? "" : translate('field.emailInUse');

    this.setState((state) => ({ 
      email: { ...state.email, errorText, isLoading: false }
    }));
      
    return isValid;
  }

  isValid = async (username) => {
    const { user } = this.props;

    const ids = await this.props.user.checkUser(username);
    return ids.length === 0 || (ids.length === 1 && ids[0]._id === user.user._id);
  }

  validUsername = async () => {
    const { translate } = this.props;
    const { username } = this.state;

    this.setState((state) => ({ username: {
      ...state.username,
      isLoading: true
    }}));

    const isValid = !username.value || await this.isValid(username.value);
    const errorText = isValid ? "" : translate('field.usernameInUse');

    this.setState((state) => ({ 
      username: { ...state.username, errorText, isLoading: false }
    }));
      
    return isValid;
  }

  handleChangeAvatar = (name, value) => {
    this.setState({ [name]: value, avatarChanged: true });
  };

  generateSecret = async () => {
    try {
      await this.props.user.generateSecret();
      this.handleOpenSecretDialog();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  handleOpenSecretDialog = () => {
    this.setState({ isOpenSecretDialog: true });
  };

  handleCloseSecretDialog = () => {
    this.setState({ isOpenSecretDialog: false });
  };

  handleSubmit = async () => {
    const {
      password,
      name,
      username,
      email,
      tel,
      address,
      city,
      postCode,
      country,
      otp,
      avatar,
      avatarChanged
    } = this.state;

    const isValid = this.validPasswords() && await this.validEmail() && await this.validUsername();
    if(!isValid) {
      return;
    }

    try {
      await this.props.user.updateUser(this.state._id, {
        email: email.value,
        password: password.value,
        name,
        username: username.value,
        tel,
        address,
        city,
        postCode,
        country,
        otp,
        avatar: avatarChanged ? avatar : null,
      });
      this.setState(buildState(this.props.user.user));
      this.props.user.showMsg("Ok.");
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  render() {
    const { classes, disabled } = this.props;
    const {
      password,
      repeatedPassword,
      username,
      name,
      email,
      tel,
      otp,
      avatar,
      address,
      city,
      postCode,
      country
    } = this.state;

    return (
      <>
        <Form onSubmit={this.handleSubmit} disabled={disabled}>
          <AvatarPicker
            field="avatar"
            onChange={this.handleChangeAvatar}
            value={avatar}
            disabled={disabled}
            tooltip={name || ""}
          />
          <TextInput
            value={name}
            field="name"
            onChange={this.handleChange}
            required
            disabled={disabled}
          />
          <div className={classes.row}>
            <div className={classes.col}>
              <TextInput
                type="email"
                value={email.value}
                field="email.value"
                onChange={this.handleChange}
                disabled={disabled || email.isLoading}
                onBlur={this.validEmail}
                errorText={email.errorText}
                required
              />
            </div>
            <div className={classes.col}>
              <TextInput
                value={tel}
                field="tel"
                onChange={this.handleChange}
                disabled={disabled}
              />
            </div>
          </div>
          <TextInput
            value={username.value}
            field="username.value"
            onChange={this.handleChange}
            required
            disabled={disabled || username.isLoading}
            onBlur={this.validUsername}
            errorText={username.errorText}
          />
          <div className={classes.row}>
            <div className={classes.col}>
              <TextInput
                value={password.value}
                field="password.value"
                type="new-password"
                onChange={this.handleChange}
                disabled={disabled}
                onBlur={this.validPasswords}
                errorText={password.errorText}
              />
            </div>
            <div className={classes.col}>
              <TextInput
                value={repeatedPassword.value}
                field="repeatedPassword.value"
                type="password"
                onChange={this.handleChange}
                disabled={disabled}
                onBlur={this.validPasswords}
                errorText={repeatedPassword.errorText}
              />
            </div>
          </div>
          <div className={classes.row}>
            <div className={classes.col}>
              <TextInput
                value={address}
                field="address"
                type="address"
                onChange={this.handleChange}
                disabled={disabled}
              />
            </div>
            <div className={classes.col}>
              <TextInput
                value={postCode}
                field="postCode"
                type="postCode"
                onChange={this.handleChange}
                disabled={disabled}
              />
            </div>
          </div>
          <div className={classes.row}>
            <div className={classes.col}>
              <TextInput
                value={city}
                field="city"
                type="city"
                onChange={this.handleChange}
                disabled={disabled}
              />
            </div>
            <div className={classes.col}>
              <TextInput
                value={country}
                field="country"
                type="country"
                onChange={this.handleChange}
                disabled={disabled}
              />
            </div>
          </div>
          {config["2fa"] && (
            <div className={classes.row}>
              <div className={classes.col}>
                <div>
                  <Switch
                    value={otp}
                    field="otp"
                    onChange={this.handleChange}
                    disabled={disabled}
                  />
                  <Button
                    color="primary"
                    disabled={disabled || !this.state.otp}
                    onClick={this.generateSecret}
                  >
                    {this.props.translate("generateSecret")}
                  </Button>
                </div>
              </div>
            </div>
          )}
        </Form>
        <Dialog
          title={this.props.translate("secret")}
          isOpen={this.state.isOpenSecretDialog}
          onConfirm={this.handleCloseSecretDialog}
        >
          <div className={classes.center}>
            {this.props.user.secret && (
              <img
                src={"data:image/jpeg;base64," + this.props.user.secret.qrCode}
                alt="secret"
                width={320}
                height={320}
              />
            )}
          </div>
        </Dialog>
      </>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  disabled: PropTypes.bool.isRequired
};

export default compose(
  withStyles(styles),
  withStore,
  withUser,
  withLocalization
)(FormWrapper);
