import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import * as serviceWorker from "./serviceWorker";
import { init } from "kd-web";
import config from "./config.json";

import UserProvider from "./providers/UserProvider";
import StoreProvider from "./providers/StoreProvider";

import { MuiPickersUtilsProvider } from "material-ui-pickers";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import MomentUtils from "@date-io/moment";
import { LocalizationProvider } from "./components/shared/localization";
import en from './localization/en-US.json';
import sk from './localization/sk-SK.json';

init(config);

const theme = createMuiTheme({
  palette: config.palette
});

class AppWrapper extends React.Component {
  componentDidMount() {
    document.documentElement.style.setProperty(
      "--primary-color",
      config.palette.primary.main
    );
    document.title = `${process.env.REACT_APP_NAME} v${process.env.REACT_APP_VERSION}`;
  }

  render() {
    return (
      <UserProvider>
        <StoreProvider>
          <LocalizationProvider
            locale={config.locale}
            en-US={en}
            sk-SK={sk}
          >
            <MuiThemeProvider theme={theme}>
              <MuiPickersUtilsProvider utils={MomentUtils}>
                <App />
              </MuiPickersUtilsProvider>
            </MuiThemeProvider>
          </LocalizationProvider>
        </StoreProvider>
      </UserProvider>
    );
  }
}

ReactDOM.render(<AppWrapper />, document.getElementById("root"));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
