import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Form from "./Form";
import Typography from "@material-ui/core/Typography";
import GithubButton from "../shared/login/GithubButton";
import GoogleButton from '../shared/login/GoogleButton';
import StravaButton from '../shared/login/StravaButton';
import FbButton from '../shared/login/FbButton';
import permissions from '../../permissions';
import config from '../../config.json';

import withUser from "../../consumers/withUser";
import { withLocalization } from '../shared/localization';
import compose from "../../compose";

const styles = theme => ({
  paper: {
    padding: theme.spacing.unit
  },
  heading: {
    marginLeft: theme.spacing.unit
  },
  connected: {
    padding: theme.spacing.unit,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start'
  }
});

function CenteredGrid(props) {
  const { user, hasPermission } = props.user;
  const disabled = !hasPermission(permissions.user);
  const connectTitle = props.translate('connect');

  return (
    <Grid container spacing={16}>
      <Grid item xs={12} sm={8}>
        <Paper className={props.classes.paper}>
          <Form disabled={disabled} />
        </Paper>
      </Grid>
      <Grid item xs={12} sm={4}>
        <Paper className={props.classes.connected}>
          <Typography variant="h6" className={props.classes.heading}>
            {props.translate('connected')}
          </Typography>
          <GithubButton config={config.github} disabled={disabled || user.github} title={props.user.user.github ? props.user.user.github.name : connectTitle} />
          <GoogleButton config={config.google} disabled={disabled || user.google} title={props.user.user.google ? props.user.user.google.name : connectTitle} />
          <FbButton config={config.fb} disabled={disabled || user.fb} title={props.user.user.fb ? props.user.user.fb.name : connectTitle} />
          <div>
            <StravaButton config={config.strava} disabled={disabled} title={props.user.user.strava ? props.translate('sync') : connectTitle} />
            {config.strava && props.user.user.strava && props.user.user.strava.synced &&
              <span> ({props.translate('lastSync')} {new Date(props.user.user.strava.synced).toLocaleDateString(props.locale)}) </span>
            }
          </div>
        </Paper>
      </Grid>
    </Grid>
  );
}

CenteredGrid.propTypes = {
  classes: PropTypes.object.isRequired
};

export default compose(withStyles(styles), withUser, withLocalization)(CenteredGrid);