import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Avatar from './Avatar';
import { getStyles } from "../style";
import withUtils from '../utils/withUtils';

const styles = theme => ({
  ...getStyles(theme),
  bigAvatar: {
    margin: theme.spacing.unit,
    width: 80,
    height: 80
  }
});

class ImageAvatars extends React.Component {
  constructor(props) {
    super(props);

    this.inputRef = React.createRef();
  }

  onChange = (event) => {
    event.preventDefault();

    const reader = new FileReader();
    reader.onloadend = async () => {
      let data = await this.props.utils.resize(reader.result, 320);
      data = data.split(',').pop();
      this.props.onChange(this.props.field, data);
    }
    reader.readAsDataURL(event.target.files[0]);
  }

  onClick = () => {
    if(!this.props.disabled) {
      this.inputRef.current.click();
    }
  }

  render() {
    const { classes, value, tooltip, disabled } = this.props;

    return (
      <div className={classes.center} style={{ cursor: disabled ? 'default' : 'pointer' }}>
        <Avatar 
          className={classes.bigAvatar}
          value={value}
          onClick={this.onClick}
          tooltip={tooltip}
          placement="right"
        />
        <input
          ref={this.inputRef}
          style={{ display: "none" }}
          type="file"
          accept="image/jpeg"
          onChange={this.onChange}
        />
      </div>
    );
  }
}

ImageAvatars.propTypes = {
  classes: PropTypes.object.isRequired,
  field: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  value: PropTypes.string.isRequired,
  tooltip: PropTypes.string.isRequired,
  disabled: PropTypes.bool
};

export default withUtils(withStyles(styles)(ImageAvatars));
