import React, { PureComponent } from "react";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import {
  PieChart, Pie, Tooltip,
} from 'recharts';
import compose from "../../compose";
import withStore from "../../consumers/withStore";
import { withTheme } from '@material-ui/core/styles';

const styles = theme => ({
  paper: {
    padding: theme.spacing.unit,
    display: 'flex',
    justifyContent: 'center',
  }
});

class Example extends PureComponent {
  render() {
    const { projectStats, userStats } = this.props.store;
    const { theme } = this.props;

    return (
      <Paper className={this.props.classes.paper}>
        <PieChart width={300} height={300}>
          <Pie dataKey="val" data={projectStats.data} cx={150} cy={150} innerRadius={80}  outerRadius={100} fill={theme.palette.secondary.main} label />
          <Pie dataKey="val" isAnimationActive={false} data={userStats.data} cx={150} cy={150} outerRadius={70} fill={theme.palette.primary.main} />
          <Tooltip />
        </PieChart>
      </Paper>
    );
  }
}

export default compose(withStore, withStyles(styles), withTheme())(Example);
